import logo from './logo.svg';
import './App.css';
import Home from './components/Home';
import { useEffect, useState } from 'react';
import Splash from './components/Splash'
function App() {
  const [flag, setFlag] = useState(true)
  useEffect(() => {
    setTimeout(() => {
      setFlag(false)
    }, 5000)
  }, [])
  return (
    <div className="App">
      {flag?<Splash />:<Home />}
    </div>
  );
}

export default App;
