import React from 'react'
import './Input.css'
import qr from './qr.jpeg'

function Input() {
  return (
    <div>
        <form className="form" method="post" action="https://docs.google.com/forms/d/e/1FAIpQLSdxXP1IQHTK4twnkRBJu45un7yHImLNUxmMDA8QO62PgX_sDA/formResponse">
        <h1>Evensia</h1><h1>CTF Registration</h1>
        <input className="input" name='entry.120767001' placeholder='Name*' required/>
        <input className="input" name='entry.2094131496' placeholder='Email*' required/>
        <input className="input" name='entry.124413857' placeholder='Whatsapp*' required/>
        <input className="input" name='entry.1648116451' placeholder='College*' required/>
        <label >Year of Study*</label>
        <div className='year'>
        <span>
            <input type='radio' className='radiobox' name='entry.266610104' value='1' checked/>
            <label>First Year</label>
        </span>
        <span>
            <input type='radio' className='radiobox' name='entry.266610104' value='2' />
            <label>Second Year</label>
        </span>
        <span>
            <input type='radio' className='radiobox' name='entry.266610104' value='3' />
            <label>Third Year</label>
        </span>
        <span>
            <input type='radio' className='radiobox' name='entry.266610104' value='4' />
            <label>Fourth Year</label>
        </span>
        </div>
        <input className="input" name='entry.650833898' placeholder='Team Name*' required/>
        <label>Team Members*</label>
        <div className='members'>
        <span>
            <input type='radio' className='radiobox' name='entry.108268883' value='1' checked/>
            <label>One</label>
        </span>
        <span>
            <input type='radio' className='radiobox' name='entry.108268883' value='2' />
            <label>Two</label>
        </span>
        <span>
            <input type='radio' className='radiobox' name='entry.108268883' value='3' />
            <label>Three</label>
        </span>
        </div>
        <input className="input" name='entry.180458615' placeholder='Member 2(Optional)' />
        <input className="input" name='entry.411347236' placeholder='Member 3(Optional)' />
        <label>UPI ID: adarshv45@fbl</label>
        <label className='note'>NOTE: Rs.100 per head</label>
        <img src={qr} alt="qr" className="qr" />
        <input className="input" name='entry.1235736812' placeholder='Transaction ID*' required/>
        <input className="submit" type="submit" value="Submit"/>
        </form>

    </div>
  )
}

export default Input